import type { LoaderFunction } from '@remix-run/node'
import { json } from '@remix-run/node'
import { Form, useLoaderData } from '@remix-run/react'
import users from '~/users.json'

type User = {
  id: number
  firstName: string
  lastName: string
}

type LoaderType = {
  users: User[]
  sortBy: keyof User
}

export const loader: LoaderFunction = async ({ request }) => {
  const url = new URL(request.url)
  const sortBy = (url.searchParams.get('sortBy') ?? 'firstName') as keyof User

  const sortedUsers = users.sort((a: User, b: User) =>
    a[sortBy] < b[sortBy] ? -1 : 1,
  )

  return json({ users: sortedUsers, sortBy })
}

export default function Index() {
  const { users, sortBy } = useLoaderData<LoaderType>()
  const handleChange = (e: any) => e.target.form.submit()

  return (
    <div className="m-2">
      <h1 className="text-2xl font-bold">Welcome to Remix Starter!</h1>
      <Form method="get" onChange={handleChange}>
        <select name="sortBy" value={sortBy}>
          <option value="firstName">First Name</option>
          <option value="lastName">Last Name</option>
        </select>
        <noscript>
          <button>Submit</button>
        </noscript>
      </Form>
      <table>
        <thead>
          <tr>
            <th>First Name</th>
            <th>Last Name</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.firstName}</td>
              <td>{user.lastName}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
